define('bapp/router', ['exports', 'ember', 'bapp/config/environment'], function (exports, Ember, config) {

  'use strict';

  var Router = Ember['default'].Router.extend({
    location: config['default'].locationType
  });

  Router.map(function () {
    this.route("scratch");
    this.resource("peers");
  });

  exports['default'] = Router;

});